on:box-shadow 300ms cubic-bezier(0, 0, 0.03, 1), -webkit-box-shadow 300ms cubic-bezier(0, 0, 0.03, 1);-webkit-box-shadow:0 24px 38px 3px rgba(0,0,0,0.03),0 9px 46px 8px rgba(0,0,0,0.03),0 11px 15px 0 rgba(0,0,0,0.02);box-shadow:0 24px 38px 3px rgba(0,0,0,0.03),0 9px 46px 8px rgba(0,0,0,0.03),0 11px 15px 0 rgba(0,0,0,0.02)}.wsu-c-card__photo-frame{width:100%}.wsu-c-card__photo{width:100%;height:225px;-o-object-fit:cover;object-fit:cover;-o-object-position:center;object-position:center}.wsu-c-card__heading{margin:0;margin-top:40px;padding:0 35px;font-size:14px;font-weight:500;line-height:1.5}.wsu-c-card__description{font-style:italic;margin:0;margin-bottom:10px;padding:0 35px;font-size:14px;font-weight:400;line-height:1.5}.wsu-c-card__address{position:relative;margin:0 10px;padding:10px 35px;font-size:13px;font-weight:300;line-height:1.5}.wsu-c-card__address::before{position:absolute;left:10px;color:#CA1237;font-family:"wsu-icons";content:""}.wsu-c-card__address-line-1,.wsu-c-card__city-state-zip{display:block}.wsu-c-card__phone-link,.wsu-c-card__email-link,.wsu-c-card__website-link{position:relative;display:block;margin:0 10px;padding:10px 35px;-webkit-transition:background-color 500ms ease;-o-transition:background-color 500ms ease;transition:background-color 500ms ease;text-decoration:none;color:#CA1237;font-size:13px;font-weight:300;line-height:1.5}.wsu-c-card__phone-link:hover,.wsu-c-card__email-link:hover,.wsu-c-card__website-link:hover{border-radius:5px;background-color:#f7f7f7;font-size:13px;font-weight:400;line-height:1.5}.wsu-c-card__phone-link:active,.wsu-c-card__phone-link:visited,.wsu-c-card__email-link:active,.wsu-c-card__email-link:visited,.wsu-c-card__website-link:active,.wsu-c-card__website-link:visited{color:#CA1237}.wsu-c-card__phone-link::before,.wsu-c-card__email-link::before,.wsu-c-card__website-link::before{position:absolute;top:50%;left:10px;-webkit-transform:translateY(-50%);-ms-transform:translateY(-50%);transform:translateY(-50%)}.wsu-c-card